<template>
    <div v-wechat-title="$route.meta.title" id="box">
        <img id="img" src="../assets/mathplanet-image.png"/>
        <div id="input-box">
            <input id="i-phone" type="text" placeholder="请输入手机号"/>
            <input id="i-code" type="text" placeholder="请输入验证码"/>
            <span id="s-send">{{msg}}</span>
        </div>
    </div>
</template>
<script>
export default {
  name: 'Login',
  data(){
      return {
          msg: '发送验证码'
      }
  }
}
</script>
<style>
    #img {
        width: 100%;
        height: 150px;
    }
    #box {
        width: 100%;
    }
    #input-box {
        margin-top: 5%;
        margin-left: 20%;
        margin-right: 20%;
    }
    input::-webkit-input-placeholder {
        text-align: center;
    }
    input[type='text'] {
        border-style: solid;
        border-radius: 15px;
        border-width: 1px;
        border-color: gray;
    }
    #i-phone {
        display: block;
        width: 100%;
        height: 35px;
    }
    #i-code {
        float: left;
        width: 50%;
        height: 25px;
        display: block;
        margin-top: 5%;
    }
    #s-send {
        float: right;
        width: 40%;
        height: 25px;
        line-height: 25px;
        background: rgb(35, 161, 245);
        color: white;
        font-size: 12px;
        display: block;
        margin-top: 5%;
        border-style: solid;
        border-radius: 15px;
        text-align: center;
    }
</style>



